import styled from "styled-components";
import { color } from "../../assets/colors";
import MainTitle from "../../components/MainTitle";
import { useAppSelector, useAppDispatch } from "../../hook";
import { asyncLogout } from "../../slices/logout";
import { useCallback } from "react";
import { loginCheck } from "../../slices/auth";
import { Cookies } from "react-cookie";
import { useNavigate, Navigate } from "react-router-dom";
const cookie = new Cookies();

const Mypage = () => {
    const navigate = useNavigate();
    const username = useAppSelector((state) => state.userAuth.username);
    const isLogin = useAppSelector((state) => state.userAuth.sessionId);
    const dispatch = useAppDispatch();

    const onLogout = useCallback(() => {
        dispatch(asyncLogout());
        cookie.remove("user", { path: "/" });
        dispatch(loginCheck());
        navigate("/");
    }, [dispatch, navigate]);

    return (
        <>
            {!isLogin && <Navigate to="/"></Navigate>}
            <Block>
                <MainTitle title="Mypage" />
                <MyPageStyle>
                    <MyInfoPage>
                        <MyInfoTabs>{username}</MyInfoTabs>
                        <LogoutButton onClick={onLogout}></LogoutButton>
                    </MyInfoPage>
                    <MyPageContent />
                </MyPageStyle>
            </Block>
        </>
    );
};

export default Mypage;

const Block = styled.div`
    width: 100%;
    max-width: 1400px;
    min-height: 100%;
    margin: 30px auto;
    padding: 0 20px;
    box-sizing: border-box;
`;
const MyPageStyle = styled.div`
    height: inherit;
    display: flex;
    align-items: flex-start;
    gap: 20px;
    margin-top: 40px;
    & > div {
        border: 1px solid ${color.gray02};
        border-radius: 10px;
        padding: 20px 10px;
        height: 100vh;
    }
`;
const MyInfoPage = styled.div`
    min-width: 250px;
    display: flex;
    flex-direction: column;
`;
const MyInfoTabs = styled.div`
    flex: 1;
`;

const MyPageContent = styled.div`
    width: 100%;
`;
const LogoutButton = styled.button`
    display: block;
    background: ${color.black02};
    color: ${color.white01};
    width: 100%;
    padding: 15px 0;
    border-radius: 5px;
`;
